import { DeleteOutlined, EditOutlined, PlusOutlined } from "@ant-design/icons";
import { useRouter } from "next/router";
import React, { useEffect, useState } from "react";
import Link from "next/link";
import {
  CError,
  CInput,
  CLoadingSpinner,
  CModal,
  CMultiSelect,
  CTable,
  CTitle,
} from "@components";
import {
  FGetAccess,
  FGetData,
  FGetTableData,
  FRemove,
  FSubmit,
} from "@function";
import { Button, Col, Row } from "antd";

const DataMenu = () => {
  const router = useRouter();
  const [data, setData] = useState([]);
  const [total, setTotal] = useState([]);
  const [menuList, setMenuList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [accessList, setAccessList] = useState({ viewAny: true });
  const [isLoading, setIsLoading] = useState(false);
  const [isModal, setIsModal] = useState(false);
  const [isSubmit, setIsSubmit] = useState(false);
  const [errors, setErrors] = useState({});
  const [forms, setForms] = useState({
    id: "",
    code: "",
    name: "",
    menu: [],
  });
  const [paginate, setPaginate] = useState({
    pageSize: 15,
    current_page: 1,
    keyword: "",
  });

  const handleInsert = (e) => {
    setIsModal(!isModal);
  };

  const handleEdit = (e) => {
    setIsModal(!isModal);
    setForms({
      id: e.id,
      code: e.code,
      name: e.name,
    });
  };

  function handleSearch(e) {
    setPaginate({
      ...paginate,
      [e.target.id]: e.target.value,
    });
  }

  const onShowSizeChange = (current, pageSize) => {
    setPaginate({
      ...paginate,
      pageSize: pageSize,
      current_page: current,
    });
  };

  const columns = [
    {
      title: "Code",
      key: "code",
      align: "center",
      dataIndex: "code",
      width: 150,
      ellipsis: true,
    },
    {
      title: "Name",
      key: "Name",
      align: "center",
      ellipsis: true,
      render: (item) => (
        <Link
          style={{ color: "#1890ff" }}
          href={router.pathname + "/" + item.id}
        >
          {item.name}
        </Link>
      ),
    },
    {
      title: "Action",
      key: "id",
      width: 120,
      align: "center",
      fixed: "right",
      render: (item) => (
        <Row justify="center">
          <Col span={8}>
            <Button
              type="primary"
              style={{ backgroundColor: "#FAAD14" }}
              icon={<EditOutlined />}
              size="small"
              onClick={() => {
                handleEdit(item);
              }}
            />
          </Col>
          <Col span={8}>
            <Button
              onClick={() => {
                FRemove(item.id, `/api/user-group`, getData);
              }}
              type="primary"
              icon={<DeleteOutlined />}
              size="small"
              danger
            />
          </Col>
        </Row>
      ),
    },
  ];

  const items = [
    {
      key: "1",
      label: (
        <div onClick={handleInsert}>
          <PlusOutlined /> Add User Group
        </div>
      ),
    },
  ];

  useEffect(() => {
    FGetAccess(router, setAccessList, setIsLoading);
    getListMenu();
  }, []);
  useEffect(() => {
    getData();
  }, [paginate]);
  const getData = () => {
    FGetTableData(setLoading, "api/user-group", paginate, setData, setTotal);
  };
  const getListMenu = () => {
    FGetData("api/list-menus", setMenuList);
  };
  const onRefresh = () => {
    setPaginate({
      pageSize: 15,
      current_page: 1,
      keyword: "",
    });
    setIsModal(false);
    onCancel();
    getData();
  };
  const handleReset = () => {
    setIsModal(false);
    onCancel();
  };

  const onCancel = () => {
    setForms({
      id: "",
      code: "",
      name: "",
      menu: [],
    });
  };

  const handleChange = (e) => {
    setForms({
      ...forms,
      [e.target.id]: e.target.value,
    });
  };

  const onMenu = (value) => {
    setForms({
      ...forms,
      menu: value,
    });
  };

  if (isLoading) return <CLoadingSpinner />;

  if (!accessList.viewAny) {
    return (
      <CError
        status={403}
        subTitle="Sorry, you are not authorized to access this page."
        labelButton="Back"
        handleBack={() => router.push("/")}
      />
    );
  }

  return (
    <>
      <CTitle />
      <CTable
        total={total}
        onShowSizeChange={onShowSizeChange}
        data={data}
        columns={columns}
        items={items}
        scroll={{ y: 500 }}
        paginate={paginate}
        handleSearch={handleSearch}
        refresh={onRefresh}
        loading={loading}
        accessPermission={accessList}
      />
      <CModal
        id={forms.id}
        title="User Group"
        onCancel={handleReset}
        isSubmit={isSubmit}
        onSubmit={() =>
          FSubmit(
            "api/user-group",
            forms,
            setErrors,
            getData,
            handleReset,
            setIsSubmit
          )
        }
        open={isModal}
      >
        <CInput
          value={forms.code}
          id="code"
          validate={errors.code}
          isRequired={true}
          label="Code"
          onChange={handleChange}
        />
        <CInput
          value={forms.name}
          validate={errors.name}
          id="name"
          isRequired={true}
          label="Name"
          onChange={handleChange}
        />
        {!forms.id > 0 && (
          <CMultiSelect
            validate={errors.menu}
            value={forms.menu}
            id="menu"
            options={menuList}
            isRequired={true}
            label="Menu"
            onChange={onMenu}
          />
        )}
      </CModal>
    </>
  );
};

export default DataMenu;
